@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Light.woff2") format("woff2");
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Mulish", sans-serif;
  background-color: #F5FAFE;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1130px;
}

.error {
  border: 2px #ff4019 solid !important;
}

.is-hidden {
  display: none !important;
}

.title {
  margin-bottom: 71px;
  font-weight: 700;
  font-size: 36px;
  text-transform: uppercase;
  text-align: center;
}

.btn {
  font-family: 'Mulish', sans-serif;
  font-size: 12px;
  color: #fff;
  border-radius: 4px;
  background-color: #EC1F46;
  transition: background-color .25s ease-in-out;
}

.btn:hover {
  background-color: #c71134;
  transition: background-color .25s ease-in-out;
}

.header {
  background-color: #fff;
  padding: 40px 0;
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin-right: 108px;
}

.header__search {
  margin-right: auto;
}

.header__burger {
  display: none;
}

.logo__img {
  width: 177px;
  height: 24px;
}

.search {
  position: relative;
  width: 350px;
  overflow: hidden;
  border-radius: 6px;
}

.search__field {
  position: relative;
  width: 100%;
  padding: 11px 75px 11px 23px;
  background: #F5FAFE;
  border: none;
  transition: opacity 0.2s;
}

.search__field:focus + .search__placeholder {
  opacity: 0;
  transition: opacity 0.2s;
}

.search__placeholder {
  position: absolute;
  left: 23px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  font-weight: 400;
  color: #333;
  opacity: 1;
  transition: opacity 0.2s;
}

.search__placeholder::after {
  content: '';
  position: absolute;
  right: -5px;
  top: 0;
  background-color: #333;
  height: 100%;
  width: 1px;
  transition: opacity 0.2s;
  animation: 0.7s infinite place;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 65px;
}

.search__img {
  width: 16px;
  height: 16px;
  fill: #fff;
  transition: all .45s;
  z-index: 100;
}

.search__img:hover {
  transform: rotate(365deg);
  transition: all .45s;
}

.user {
  display: flex;
  align-items: center;
}

.user__avatar {
  margin-right: 18px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.user__name {
  font-size: 14px;
  font-weight: 400;
  color: #333;
}

@keyframes place {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.nav {
  padding: 31px 0;
  background-color: #0a223d;
}

.nav__list {
  display: flex;
  justify-content: space-between;
}

.nav__link {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  transition: color .25s ease;
}

.nav__link:hover {
  color: #EC1F46;
  transition: color .25s ease;
}

.breadcrumb {
  padding: 19px 0;
}

.breadcrumb__list {
  display: flex;
  align-items: center;
  font-size: 10px;
  color: #333;
}

.breadcrumb__item:not(:last-child):after {
  content: "/";
  margin-right: 4px;
}

.hotel {
  padding-top: 17px;
  padding-bottom: 70px;
}

.hotel__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}

.hotel__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 2px;
}

.hotel__stars {
  flex-basis: 100%;
  margin-bottom: 4px;
}

.hotel__stars-img {
  width: 8px;
  height: 8px;
}

.hotel__title {
  font-weight: 700;
  font-size: 24px;
  color: #333;
  text-transform: uppercase;
  margin-right: 15px;
}

.hotel__descr {
  font-size: 12px;
  line-height: 15px;
  color: #333;
}

.hotel__rating {
  margin-top: 5px;
}

.hotel__grid {
  display: flex;
  justify-content: space-between;
}

.hotel__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 450px;
  margin-right: 30px;
  border-radius: 8px;
  margin-left: 0;
}

.hotel__slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-position: center;
  background-size: 10px;
  background-repeat: no-repeat;
  z-index: 99;
}

.hotel__slider-btn--next {
  right: 16px;
  background-image: url("../img/btn-next.svg");
}

.hotel__slider-btn--prev {
  left: 18px;
  background-image: url("../img/btn-prev.svg");
}

.hotel__slide {
  position: relative;
}

.hotel__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hotel__slide:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 122, 223, 0.48) 0%, rgba(255, 255, 255, 0.128) 100%);
  z-index: 10;
}

.hotel__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.hotel__booking {
  font-family: 'Nunito', sans-serif;
  display: flex;
  padding: 32px 28px 26px 27px;
  width: 350px;
  min-height: 210px;
  background-color: #0a223d;
  color: #fff;
  border-radius: 8px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.hotel__room {
  margin-top: 3px;
}

.hotel__price {
  display: flex;
  flex-direction: column;
}

.hotel__starts {
  font-size: 10px;
  font-weight: 400;
  margin-bottom: 4px;
}

.hotel__pricetag {
  font-size: 24px;
  font-weight: 800;
}

.hotel__how {
  font-size: 12px;
  font-weight: 400;
}

.hotel__button {
  align-self: flex-end;
  padding: 13px 30px;
  width: 100%;
  min-height: 45px;
  font-weight: 600;
  font-size: 14px;
  border-radius: 4px;
}

.hotel__call {
  display: flex;
  justify-content: space-between;
  margin-top: 2px;
  width: 100%;
  font-weight: 600;
  font-size: 18px;
}

.hotel__call-text {
  display: inline-block;
}

.hotel__call-link {
  position: relative;
  display: inline-block;
}

.hotel__call-link::after {
  content: '';
  position: absolute;
  left: -23px;
  top: 4px;
  width: 14px;
  height: 14px;
  background-image: url("../img/call.svg");
}

.hotel__map {
  width: 347px;
  height: 213px;
  background-color: #0a223d;
  border-radius: 10px;
  overflow: hidden;
  background-image: url("../img/map-preload.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hotel__map-frame {
  width: 100%;
  height: 100%;
}

.rating {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 65px;
  min-height: 45px;
  padding: 7px;
  background-color: #EC1F46;
  border-radius: 4px;
  color: #fff;
}

.rating__name {
  margin-bottom: 3px;
  font-size: 8px;
}

.rating__counter {
  font-size: 14px;
}

.offer {
  padding: 5px 9px;
  color: #fff;
  font-weight: 600;
  font-size: 10px;
  background-color: #EC1F46;
  border-radius: 3px;
}

.room {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 12px;
  font-weight: 400;
}

.room__text {
  position: relative;
}

.room__text--guest {
  margin-bottom: 11px;
}

.room__text--guest::after {
  content: '';
  position: absolute;
  left: -25px;
  top: 0;
  width: 14px;
  height: 14px;
  background-image: url("../img/guest.svg");
}

.room__text--room::after {
  content: '';
  position: absolute;
  left: -27px;
  top: -1px;
  width: 14px;
  height: 14px;
  background-image: url("../img/home.svg");
}

.room__text--geo::after {
  content: '';
  position: absolute;
  left: -27px;
  top: -1px;
  width: 14px;
  height: 14px;
  background-image: url("../img/location.svg");
}

.newsletter {
  position: relative;
  z-index: 10;
  padding: 99px 0 88px;
  background-image: url("../img/newsletter-bg.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
}

.newsletter::after {
  content: '';
  position: absolute;
  z-index: -1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(10, 34, 61, 0.8);
}

.newsletter__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.newsletter__text {
  margin-right: 139px;
}

.newsletter__subtitle {
  display: inline-block;
  margin-bottom: 5px;
  font-weight: 300;
  font-size: 24px;
}

.newsletter__title {
  font-weight: 600;
  font-size: 36px;
  text-transform: uppercase;
}

.subscribe {
  position: relative;
  width: 350px;
  height: 55px;
  overflow: hidden;
  border-radius: 4px;
}

.subscribe__field {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0 85px 0 33px;
  background: #fff;
  border: none;
}

.subscribe__field::placeholder {
  font-weight: 300;
  font-size: 14px;
  color: 8d8d8d;
}

.subscribe__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 65px;
  height: 105%;
  font-weight: 700;
  font-size: 12px;
}

.reviews {
  padding: 43px 0 70px;
}

.reviews__title {
  margin-bottom: 71px;
  font-weight: 700;
  font-size: 36px;
  text-transform: uppercase;
  text-align: center;
}

.reviews__slider {
  position: relative;
}

.reviews__slider-btn {
  position: absolute;
  top: 46%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-position: center;
  background-size: 10px;
  background-repeat: no-repeat;
  z-index: 99;
}

.reviews__slider-btn--next {
  right: 18px;
  background-image: url("../img/reviews-right.svg");
}

.reviews__slider-btn--prev {
  left: 18px;
  background-image: url("../img/reviews-left.svg");
}

.reviews__item {
  position: relative;
  margin: 45px auto 0;
  margin-top: 45px;
  width: 730px;
  min-height: 250px;
  padding-bottom: 29px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-radius: 8px;
}

.reviews__item::before {
  content: '';
  position: absolute;
  top: 13px;
  left: 64px;
  width: 70px;
  height: 48px;
  background-image: url("../img/quote-left.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.reviews__item::after {
  content: '';
  position: absolute;
  bottom: 113px;
  right: 60px;
  width: 70px;
  height: 48px;
  background-image: url("../img/quote-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.reviews__profile {
  margin-top: -45px;
  margin-bottom: 29px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.reviews__avatar {
  margin-bottom: 11px;
  width: 90px;
  height: 90px;
  object-fit: cover;
  border-radius: 50%;
}

.reviews__username {
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 12px;
  color: #EC1F46;
}

.reviews__date {
  margin-bottom: -3px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #000;
}

.reviews__stars-img {
  margin: 0 -1px;
}

.reviews__text {
  max-width: 537px;
  font-weight: normal;
  font-size: 12px;
  line-height: 160%;
  text-align: justify;
  color: #333;
}

.activities {
  padding: 62px 0 98px;
}

.activities__title {
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  color: #000;
  margin-bottom: 58px;
}

.card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(255px, max-content);
  gap: 30px;
}

.card__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding: 25px 27px 24px 27px;
  overflow: hidden;
  border-radius: 6px;
}

.card__item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(179.83deg, rgba(196, 196, 196, 0) 35.34%, #0A223D 99.85%);
  z-index: -1;
}

.card__img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
